@import "https://fonts.googleapis.com/css2?family=Raleway:wght@700&family=Roboto:wght@400;500;700;900&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a, button {
  cursor: revert;
}

ol, ul, menu {
  list-style: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

table {
  border-collapse: collapse;
}

input, textarea {
  -webkit-user-select: auto;
}

textarea {
  white-space: revert;
}

meter {
  -webkit-appearance: revert;
  -ms-appearance: revert;
  appearance: revert;
}

:where(pre) {
  all: revert;
}

::-ms-input-placeholder {
  color: unset;
}

::placeholder {
  color: unset;
}

::marker {
  content: initial;
}

:where([hidden]) {
  display: none;
}

:where([contenteditable]:not([contenteditable="false"])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}

:where([draggable="true"]) {
  -webkit-user-drag: element;
}

:where(dialog:modal) {
  all: revert;
}

:root {
  --text-font-family: Roboto;
  --menu-select-color: #000;
  --select-color: #b92f2c;
  --title-color: #000;
  --description-color: #b92f2c;
}

.section {
  width: 100%;
  max-width: 1280px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.container {
  width: 100%;
  max-width: 1216px;
  margin: 0 auto;
  padding: 0;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
  padding: 0;
  display: flex;
}

.gallery__items {
  width: 384px;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

.gallery__items:hover, .gallery__items:focus {
  border: 5px solid #b92f2c;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  scale: .95;
}

.gallery__items__img img {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.gallery__items__details {
  height: 100px;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 10px;
  margin-left: 10px;
  display: flex;
}

.gallery__items__details--title {
  color: var(--title-color);
  font-family: var(--text-font-family);
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.gallery__items__details--genres {
  color: var(--select-color);
  font-family: var(--text-font-family);
  margin-top: 0;
  margin-bottom: 0;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.navigation {
  gap: 8px;
  display: flex;
}

.navigation_watched {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #b92f2c;
  border-radius: 100px;
  margin-left: 706px;
  padding: 14px 32px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  box-shadow: 0 0 7px rgba(27, 27, 27, .15);
}

.navigation_queue {
  color: #b92f2c;
  text-align: center;
  text-transform: uppercase;
  background: #fff;
  border-radius: 100px;
  padding: 14px 24px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.navigation__home {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #000;
  border-radius: 100px;
  padding: 14px 32px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.navigation__home--from-my-library {
  color: #000;
  background-color: #fff;
}

.navigation__library {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  background: #fff;
  border-radius: 100px;
  align-items: flex-start;
  gap: 10px;
  padding: 14px 24px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.navigation__library--from-my-library {
  color: #fff;
  background-color: #000;
}

.load-more {
  color: #fff;
  text-align: center;
  cursor: pointer;
  width: 200px;
  background: #b92f2c;
  border: none;
  border-radius: 20px;
  align-items: center;
  margin: 20px;
  padding: 10px;
  font-size: 16px;
}

.load-more:hover, .load-more:focus {
  background: #7b1f1d;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.footer__main__button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 14px 28px;
}

.footer__main__button:hover, .footer__main__button:focus {
  outline: none;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

@keyframes heartbeat {
  0% {
    transform: scale(.75);
  }

  20% {
    transform: scale(1);
  }

  40% {
    transform: scale(.75);
  }

  60% {
    transform: scale(1);
  }

  80% {
    transform: scale(.75);
  }

  100% {
    transform: scale(.75);
  }
}

.heart {
  width: 14px;
  height: 12.88px;
  animation: heartbeat 1s infinite;
}

@keyframes reverse-scale {
  0% {
    transform: scaleY(1);
  }

  100% {
    transform: scaleY(-1);
  }
}

.logo__svg {
  animation: reverse-scale 4s infinite;
}

.modal {
  height: 100vh;
  width: 100vw;
  align-content: center;
  justify-content: center;
  margin-top: 10%;
  display: flex;
  position: relative;
  overflow: hidden;
}

.main__modal__container {
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  background: #f0f0f3;
  border: 1px solid #f0f0f3;
  border-radius: 50px;
  transition: all .4s ease-in-out;
  box-shadow: 5px 7px 7px rgba(0, 0, 0, .035);
}

.main__modal__container:hover {
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  border: 1px solid rgba(255, 111, 0, .098);
  transition: all .4s ease-in-out;
  box-shadow: 0 4px 15px rgba(255, 111, 0, .098);
}

#modal-container {
  height: 100%;
  width: 100%;
  z-index: 1;
  display: table;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(0);
}

#modal-container.one {
  animation: unfoldIn 1s cubic-bezier(.165, .84, .44, 1) forwards;
  transform: scaleY(.01)scaleX(0);
}

#modal-container.one .modal-background .modal {
  animation: zoomIn .5s cubic-bezier(.165, .84, .44, 1) .8s forwards;
  transform: scale(0);
}

#modal-container.one.out {
  animation: unfoldOut 1s cubic-bezier(.165, .84, .44, 1) .3s forwards;
  transform: scale(1);
}

#modal-container.one.out .modal-background .modal {
  animation: zoomOut .5s cubic-bezier(.165, .84, .44, 1) forwards;
}

#modal-container .modal-background {
  text-align: center;
  vertical-align: middle;
  background: rgba(0, 0, 0, .098);
  display: table-cell;
}

.content {
  min-height: 100%;
  height: 100%;
  z-index: 0;
  position: relative;
}

.content .buttons {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
  padding: 0;
}

.content .buttons .members-modal-button {
  text-align: center;
  cursor: pointer;
  border-radius: 3px;
  margin: 10px;
  padding: 10px 15px;
  font-size: 18px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: relative;
}

.content .buttons .members-modal-button:hover {
  color: #ff6c00;
}

.content .buttons .members-modal-button:hover:after {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

.content .buttons .members-modal-button:after {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 100% 100%;
  background-color: #ff6c00;
  transition: transform .25s ease-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

@keyframes unfoldIn {
  0% {
    transform: scaleY(.005)scaleX(0);
  }

  50% {
    transform: scaleY(.005)scaleX(1);
  }

  100% {
    transform: scaleY(1)scaleX(1);
  }
}

@keyframes unfoldOut {
  0% {
    transform: scaleY(1)scaleX(1);
  }

  50% {
    transform: scaleY(.005)scaleX(1);
  }

  100% {
    transform: scaleY(.005)scaleX(0);
  }
}

@keyframes zoomIn {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes zoomOut {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@media screen and (min-width: 1900px) {
  .flickity-gallery {
    display: none;
  }

  .main__modal {
    height: 100%;
    width: 100%;
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 0;
    left: 0;
  }

  .main__modal > div {
    height: 90%;
    align-items: center;
    justify-items: center;
    display: grid;
  }

  .main__modal__container {
    height: 55%;
    align-content: center;
    display: grid;
    position: relative;
  }

  .main__modal__container h2 {
    max-height: 45px;
    margin: 0;
    font-size: 40px;
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
  }

  .main__modal__container h2 img {
    transform: translateY(8%);
  }

  .main__modal__container__close-button {
    text-align: center;
    height: 40px;
    width: 40px;
    border: 1px solid #141414;
    border-radius: 100%;
    font-size: 25px;
    line-height: 40px;
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 46px;
    right: 40px;
  }

  .main__modal__container__close-button:hover {
    cursor: pointer;
    filter: progid:DXImageTransform. Microsoft. gradient(startColorstr= "#ffc99e", endColorstr= "#ffc99e", GradientType= 1);
    color: #ff6c00;
    background: linear-gradient(138deg, #ffc99e 0%, #ffe9d7 15%, rgba(255, 255, 255, 0) 50%, #ffe9d7 85%, #ffc99e 100%);
    border-color: #ff6c00;
    animation: rotating-circle 2s infinite;
  }

  @keyframes rotating-circle {
    0% {
      transform: rotate(0deg);
    }

    50% {
      transform: rotate(180deg);
    }

    100% {
      transform: rotate(-180deg);
    }
  }

  .main__modal__container__people {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-auto-flow: column;
    align-items: center;
    display: grid;
  }

  .main__modal__container__people figure {
    text-align: center;
    width: 100%;
    background-color: #fff;
    border-radius: 10px;
    margin: 0;
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
    position: relative;
    overflow: hidden;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .204);
  }

  .main__modal__container__people figure img {
    object-fit: cover;
    width: 100%;
    height: 250px;
  }

  .main__modal__container__people figure figcaption {
    padding: 5%;
    font-size: 14px;
    font-weight: 900;
  }

  .main__modal__container__people figure figcaption span {
    color: gray;
    font-size: smaller;
    font-weight: 500;
  }

  .main__modal__container__people figure:hover, .main__modal__container__people figure:focus {
    transform: scale(1.025);
  }

  .main__modal__container__people a {
    height: 40px;
    text-decoration: none;
  }

  .main__modal__container__people svg {
    color: #ccc;
    border-radius: 50px;
    margin-top: 5px;
    padding: 12px;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
    overflow: visible;
  }

  .main__modal__container__people svg:hover, .main__modal__container__people svg:focus {
    color: #fff;
    background-color: #2e2e2e;
  }

  .main__modal__container__people p {
    max-width: 180px;
    margin: 0;
  }

  .main__modal__container__people--1 {
    transform: scale(.85)translate(9%, -3%);
  }

  .main__modal__container__people--2, .main__modal__container__people--4 {
    transform: scale(.92);
  }

  .main__modal__container__people--5 {
    transform: scale(.85)translate(-9%, -3%);
  }
}

