.modal-gallery {
  opacity: 1;
  transition: var(--transition);
  background: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.gallery-is-hidden {
  opacity: 0;
  visibility: hidden;
}

.frame {
  width: 450px;
  height: 609px;
  background: #fff;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.forms__title {
  letter-spacing: .01em;
  color: var(--social-mail-icons--normal-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.modal-content {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: flex-start;
  padding: 40px;
  display: flex;
}

.container__content {
  width: 1280px;
  height: 77px;
  background: #f7f7f7;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  width: 1216px;
  height: 316px;
  background-image: -webkit-image-set(url("header-background-img-d.f027336c.png"), url("header-background-img-d@2x.a80f099f.png") 2x);
  background-image: image-set("header-background-img-d.f027336c.png", "header-background-img-d@2x.a80f099f.png" 2x);
  margin-left: auto;
  margin-right: auto;
  padding: 16px 32px 32px;
}

.logo {
  width: 375px;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 151px;
  text-decoration: none;
  display: flex;
}

